import React from "react";
import { segment, stateToColor } from "./utils";

function Row(props) {
  return (
    <div className="flex">
      {props.keymap.map((entry, i) => {
        const { key, state } = entry;
        const color = stateToColor(state);
        return (
          <div
            onClick={(e) => props.onKeypress(key)}
            className={"cursor-pointer w-4 h-6 m-1 px-1 py-1/2 " + color}
            key={key}
          >
            {key}
          </div>
        );
      })}
    </div>
  );
}
export default function Keyboard(props) {
  const segments = segment(props.keymap, 5);
  return (
    <div className="w-full border-2 border-slate-200">
      <div className="py-4 mx-2 flex flex-col overflow-x-scroll">
        {segments.map((s, i) => (
          <Row key={i} keymap={s} onKeypress={props.onKeypress} />
        ))}
        <div className="flex">
          <div
            className="cursor-pointer px-1 h-6 m-1 py-1/2 bg-slate-200"
            onClick={props.onEnter}
          >
            Enter
          </div>
          <div
            className="cursor-pointer px-1 h-6 m-1 py-1/2 bg-slate-200"
            onClick={props.onDelete}
          >
            Delete
          </div>
        </div>
      </div>
    </div>
  );
}
