import React from "react";

export default function Modal(props) {
  if (!props.show) {
    return null;
  }
  return (
    <div className="z-10 fixed bg-opacity-70 bg-white h-screen w-full">
      <div className="m-4 p-2 bg-white">
        <div
          className="absolute right-6 cursor-pointer z-50 underline"
          onClick={props.onClose || (() => {})}
        >
          Close
        </div>
        {props.children}
      </div>
    </div>
  );
}
